import { Component, OnInit } from '@angular/core';
import { Person } from '../person';
@Component({
    selector: 'app-vs',
    templateUrl: './vs.component.html',
    styleUrls: ['./vs.component.css']
})
export class VsComponent implements OnInit {
    public t1: string[] = ['ÂåÖÂ≠ê', 'Á≤âÁå™', 'ÈòøËâ∫', 'ÈòøËç£', 'Êàê‰∏ä', 'Êµ™‰∫∫'];
    public t2: string[] = ['‰∏çÊáÇ', 'Â©∑Â©∑', 'Âù§', 'Sunny', 'Êµ©Â≠ê', 'Âä®G'];
    public t3: string[] = ['Â∞èÁé≤', 'Ëä±Ëä±', 'Â∞èÂ∞èÊñπ', 'Êú®Êòì', 'Rain', 'Ê∂õÊ∂õ'];
    public t4: string[] = ['Poppy', 'Âè∂Â≠ê', 'Â§úÊΩáÊû´', 'Ë±ÜÊ≤ôÂåÖ', 'Ê±óÊ±ó', 'Â∞èÁä¨'];
    public t5: string[] = ['ÂÖ≥ÂÖ≥', 'Êú±Âè§Âäõ', 'ÂàòÂ®Å', 'Áëû', 'ÈòøÊåØ', 'Âè™‰∏∫Á≠â‰Ω†'];
    public all: any[] = [];
    public teamNum: string[] = ['A', 'B', 'C', 'D', 'E'];
    constructor() { }


    public refresh(t): void {
        for (let i in t.score) {
            t.score[i] = null;
        }
    }



    public count(t, index): void {
        t.member.forEach(element => {
            element.s1 = 0;
        });
        for (let index = 1; index < 12; index += 2) {
            let current = t.score['s' + index.toString()];
            let next = t.score['s' + (index + 1).toString()];
            console.log(index + '+current:' + current);
            console.log(index + '+next:' + next);
            if (current && next) {
                if ((current < 22 && next < 22) && (current == 21 || next == 21) && (current != next)) {
                    t.hasError = false;
                } else {
                    t.message = 'Á¨¨' + Math.ceil(index / 2) + 'Â±Ä'
                    t.hasError = true;
                    return;
                }
            }
        }
        if (t.hasError) {
            return;
        } else {
            t.showScore = !t.showScore;
        }
        t.score.s1 > t.score.s2 ? this.addScore(t, Math.abs(t.score.s1 - t.score.s2), 0, 1) : this.addScore(t, Math.abs(t.score.s1 - t.score.s2), 2, 3);
        t.score.s3 > t.score.s4 ? this.addScore(t, Math.abs(t.score.s3 - t.score.s4), 0, 2) : this.addScore(t, Math.abs(t.score.s3 - t.score.s4), 4, 5);
        t.score.s5 > t.score.s6 ? this.addScore(t, Math.abs(t.score.s5 - t.score.s6), 1, 4) : this.addScore(t, Math.abs(t.score.s5 - t.score.s6), 3, 5);
        t.score.s7 > t.score.s8 ? this.addScore(t, Math.abs(t.score.s7 - t.score.s8), 0, 3) : this.addScore(t, Math.abs(t.score.s7 - t.score.s8), 1, 2);
        t.score.s9 > t.score.s10 ? this.addScore(t, Math.abs(t.score.s9 - t.score.s10), 0, 4) : this.addScore(t, Math.abs(t.score.s9 - t.score.s10), 2, 5);
        t.score.s11 > t.score.s12 ? this.addScore(t, Math.abs(t.score.s11 - t.score.s12), 2, 5) : this.addScore(t, Math.abs(t.score.s11 - t.score.s12), 3, 4);
        localStorage.setItem('all', JSON.stringify(this.all));
    }

    private addScore(t, num, member1, member2) {
        t.member[member1].s1 += num;
        t.member[member2].s1 += num;
    }

    ngOnInit() {
        if (localStorage.getItem('all')) {
            this.all = JSON.parse(localStorage.getItem('all'));
        } else {
            let id = 0;
            for (let i = 1; i < 6; i++) {
                let temp = [];
                let teamName = 't' + i;
                this[teamName].forEach(element => {
                    const p = new Person({
                        id: id++,
                        name: element,
                        s1: 0
                    });
                    temp.push(p);
                });
                this.all.push(
                    {
                        member: temp,
                        showScore: false,
                        message: '',
                        score: {
                            s1: null,
                            s2: null,
                            s3: null,
                            s4: null,
                            s5: null,
                            s6: null,
                            s7: null,
                            s8: null,
                            s9: null,
                            s10: null,
                            s11: null,
                            s12: null
                        }
                    }
                );
            }
            localStorage.setItem('all', JSON.stringify(this.all));
        }
    }
}



